            removeFirst();
            let hm = document.createElement("script");
            hm.src = "https://hm.baidu.com/hm.js?0f288bdbb13003ca0db28bde85bee1cb";
            hm.id = "baidu_tj";
            let s = document.getElementsByTagName("script")[0];
            s.parentNode.insertBefore(hm, s);
